@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC&family=Noto+Serif+TC:wght@400;500&family=Roboto+Mono&display=swap);
html {
  height: 100%;
}
body {
  font-family: "Roboto Mono", monospace;
  position: relative;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  position: relative;
  height: 100%;
}
.App {
  overflow: hidden;
  position: relative;
  height: 100vh;
  display: flex;
  box-sizing: border-box;
}
.f-list {
  overflow-x: hidden;
  overflow-y: auto;
  flex: 0 0 250px;
  padding: 5px;
}
@media screen and (max-width: 768px) {
  .f-list {
    display: none !important;
  }
  .menu {
    display: block !important;
  }
}
.menu {
  display: none;
}
.f-list li {
  margin: 5px 0;
}
.f-list li:hover .none {
  display: block;
}

.f-list .btn {
  position: fixed;
  bottom: 5px;
}
.f-list .btn svg {
  vertical-align: text-bottom;
}
.f-list .btn button {
  background: none;
  border: none;
  cursor: pointer;
}
.f li {
  cursor: pointer;
}
.n {
  flex: 3 1;
  display: flex;
}
ul {
  list-style: none;
}
.n-list {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 5px 15px;
  flex: 0 0 250px;
}
.n-list ul {
  padding: 0;
  margin: 25px 0;
}
.n-list li {
  padding: 5px;
}
.n-detail {
  overflow: hidden;
  position: relative;
  height: 100%;
  padding: 5px;
  flex: 2 1;
}
.svgBtn {
  cursor: pointer;
}
.svgBtnDisable svg {
  cursor: default;
  fill: gray;
}
.right {
  float: right;
}
a {
  text-decoration: none;
}
/* 超過換行 */
a > div {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 200px;
}
.list-svg {
  margin-right: 5px;
}
.yellow {
  fill: goldenrod !important;
}
.t-bold {
  font-weight: bold;
}
.t-small {
  font-size: small;
}
.e-btn {
  cursor: pointer;
  border: none;
  margin: 0 5px;
}
.superFancyBlockquote {
  color: #999;
  /* font-family: "Hoefler Text", Georgia, serif; */
  font-style: italic;
  text-align: center;
}
.Editor {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  width: 100%;
  box-sizing: content-box;
}
.EditorBtn {
  flex: 0 0 50px;
  text-align: center;
  margin: 10px 0;
}
.DraftEditor-root {
  flex: 1 1;
  overflow: auto;
  cursor: text;
  padding: 10px;
}

div.switch {
  display: inline-block;
  z-index: 1;
  position: absolute;
  right: 55px;
}
div.switch > input {
  position: absolute;
  background-color: #ccc;
  opacity: 0;
  z-index: 1;
  border-radius: 15px;
}
div.switch > label {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 26px;
  border-radius: 15px;
  background: #bebebe;
}
div.switch > label::after {
  cursor: pointer;
  position: absolute;
  content: "";
  background-color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  top: 3px;
  left: 5px;
  transition: 0.2s;
  box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.2);
}
input:checked + label::after {
  left: 18px;
}
input:checked + label {
  background: #000;
}
div {
  box-sizing: border-box;
}
.msg {
  margin: 10px 30px;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: smaller;
  background-color: rgb(255, 229, 150);
}
.none {
  cursor: pointer;
  display: none;
}
.active + li {
  border-color: transparent !important;
}
.active {
  background-color: rgb(255, 229, 150);
  border-radius: 5px;
  border-color: transparent !important;
}

.active a {
  color: #000 !important;
}
.active .list-svg {
  fill: #000 !important;
}

